import { initializeApp } from 'firebase/app';
import { getDatabase, ref, set, push, onValue } from 'firebase/database';

// Firebase konfigurcis adataink (cserld ki a sajt adataiddal)
const firebaseConfig = {
    apiKey: "AIzaSyApLu5bmYByHbdzuuQ7Qged9Qj8dpgi570",
    authDomain: "esemenyrendezo-71f5b.firebaseapp.com",
    databaseURL: "https://esemenyrendezo-71f5b-default-rtdb.europe-west1.firebasedatabase.app",
    projectId: "esemenyrendezo-71f5b",
    storageBucket: "esemenyrendezo-71f5b.firebasestorage.app",
    messagingSenderId: "185020967631",
    appId: "1:185020967631:web:a501cb8e6695bda12625ba",
    measurementId: "G-X9VW5Y4CS4"
  };

// Firebase alkalmazs inicializlsa
const app = initializeApp(firebaseConfig);
const database = getDatabase(app);

// zenet kldse Firebase-be
function sendMessage(messageText, userId) {
  const newMessageRef = push(ref(database, 'chats/'));  // 'chats' a mappa, ahol az zeneteket troljuk
  set(newMessageRef, {
    userId: userId,      // Felhasznl azonostja
    message: messageText,  // zenet szvege
    timestamp: Date.now(),  // Idblyeg, hogy mikor kldtk
  });
}

// zenetek lekrse Firebase-bl s figyelse vals idben
function listenForMessages(setMessages) {
  const messagesRef = ref(database, 'chats/');  // A 'chats' mappban trolt zenetek lekrse
  onValue(messagesRef, (snapshot) => {
    const data = snapshot.val();  // Az sszes zenet
    const messages = [];
    for (let key in data) {
      messages.push(data[key]);  // zenetek hozzadsa az llapothoz
    }
    setMessages(messages);  // zenetek llapotban val frisstse
  });
}

// Firebase.js exportlsa, hogy ms fjlokban hasznlni tudjuk
export { sendMessage, listenForMessages };